import { Component, Inject } from "@angular/core";
import { FormArray, FormControl, FormGroup, Validators } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { ManageLoadingService } from "@tei/core";
import { BreadCrumb, SnackBarService } from "@tei/material";
import { firstValueFrom, Subscription } from "rxjs";
import { IHostAdminCenterHelperService } from "../../../@intermediate/host-admin-center-helper.interface";
import { GetRangeDeviceSetupResponse, GetRangeSitesForm, RangeDeviceSetupForm } from "../../../@intermediate/models/get-range-device-setup.model";
import { GetRangeSitesResponse } from "../../../@intermediate/models/get-range-sites.model";
import { portalGroupDetails } from "../../../@intermediate/models/host-admin-center-helper.model";
import { PostGRangeDeviceSetupRequest } from "../../../@intermediate/models/post-range-device-setup.model";
import { PostRangeSiteDeviceSetupRequest } from "../../../@intermediate/models/post-range-site-device-setup.model";
import { IRangeDeviceSetupService } from "../../../@intermediate/range-device-setup.interface";

@Component({
  selector: 'tei-admin-admin-center-range-device-setup',
  templateUrl: './range-device-setup.component.html',
  styleUrls: ['./range-device-setup.component.scss']
})

export class RangeDeviceSetupComponent
{
  setupID?: number;
  breadCrumbData: BreadCrumb[] = [];
  subscription: Subscription = new Subscription();
  rangeSitesDeviceSetupList: GetRangeSitesResponse[];
  displayedColumns: string[] = ['SiteName', 'UserName', 'Password'];
  portalDetail: portalGroupDetails;
  settingForm = this.createSettingForm();
  private createSettingForm(): FormGroup<RangeDeviceSetupForm>
  {
    return new FormGroup<RangeDeviceSetupForm>({
      URL: new FormControl('', Validators.required),
      ComputerName: new FormControl('', Validators.required),
      BackupURL: new FormControl('', Validators.required),
      IsEnabled: new FormControl(false),
      SourceGRangeSites: new FormArray<FormGroup<GetRangeSitesForm>>([])
    });
  }
  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    @Inject("IRangeDeviceSetupService") private rangeDeviceSetupService: IRangeDeviceSetupService,
    private snackBar: SnackBarService,
    private translate: TranslateService,
    public manageLoadingService: ManageLoadingService)
  {

  }

  ngOnInit(): void
  {
    this.InitData();
  }

  get sourceGRangeSites(): FormArray<FormGroup<GetRangeSitesForm>>
  {
    return this.settingForm?.get('SourceGRangeSites') as FormArray<FormGroup<GetRangeSitesForm>>;
  }

  private async InitData(): Promise<void>
  {
    await this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = portalDetail;
        this.fillBreadCrump(portalDetail);
        this.loadData();
      }
    }));
  }

  private async loadData(): Promise<void>
  {
    try
    {
      this.manageLoadingService.startLoading();
      const response = await firstValueFrom(this.rangeDeviceSetupService.Get({ PortalID: this.portalDetail.portal.PortalID }));
      this.initForm(response);

    }
    catch (error)
    {
      console.error("Error loading data:", error);
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }

  }

  private createFormGroup(response?: GetRangeDeviceSetupResponse): FormGroup<RangeDeviceSetupForm>
  {
    return new FormGroup<RangeDeviceSetupForm>({
      URL: new FormControl<string>(response?.URL || '', [Validators.required]),
      ComputerName: new FormControl<string>(response?.ComputerName || '', [Validators.required]),
      BackupURL: new FormControl<string>(response?.BackupURL || '', [Validators.required]),
      IsEnabled: new FormControl<boolean>(response?.IsEnabled || false),
      SourceGRangeSites: new FormArray<FormGroup<GetRangeSitesForm>>(
        response?.GRangeSites.map(setting => this.createSiteFormGroup(setting)) || []
      )
    });
  }

  private createSiteFormGroup(setting): FormGroup<GetRangeSitesForm>
  {
    return new FormGroup<GetRangeSitesForm>({
      GRangeSiteID: new FormControl<number>(setting.GRangeSiteID),
      SiteID: new FormControl<string>(setting.SiteID),
      SiteName: new FormControl<string>(setting.SiteName),
      UserName: new FormControl<string>(setting.UserName, [Validators.required]),
      Password: new FormControl<string>(setting.Password, [Validators.required])
    });
  }

  private initForm(response: GetRangeDeviceSetupResponse): void
  {
    this.settingForm = this.createFormGroup(response);
    this.rangeSitesDeviceSetupList = response.GRangeSites;
  }

  async saveSetting(): Promise<void>
  {
    try
    {
      this.manageLoadingService.startLoading();

      const request: PostGRangeDeviceSetupRequest = {
        ...this.settingForm?.value as PostGRangeDeviceSetupRequest,
        PortalID: this.portalDetail?.portal?.PortalID,
        SourceGRangeSites: this.settingForm?.value.SourceGRangeSites as PostRangeSiteDeviceSetupRequest[]
      };

      await firstValueFrom(this.rangeDeviceSetupService.Post(request));
      this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGE_SETUP.SAVED_SUCCESSFULLY'), '', null, 'left');
    }
    catch (error)
    {
      console.error("Error saving settings:", error);
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }
  }

  fillBreadCrump(portalDetail): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../../../',
      },
      {
        title: `${ portalDetail.parentPortalGroup.PortalGroup.PortalGroupName }`,
        relativeUrl: `../../../../../`,
      },
      {
        title: `${ portalDetail.portal.PortalName }`,
        relativeUrl: '../../../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACCESS'),
        relativeUrl: '../../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.TITLE'),
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGEMACHINEINTEGRATION'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGE_SETUP.TITLE'),
        relativeUrl: '',
      }
    ];
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
