import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetRangeDeviceSetupRequest, GetRangeDeviceSetupResponse } from '../@intermediate/models/get-range-device-setup.model';
import { PostGRangeDeviceSetupRequest, PostGRangeDeviceSetupResponse } from '../@intermediate/models/post-range-device-setup.model';
import { IRangeDeviceSetupService } from '../@intermediate/range-device-setup.interface';

@Injectable()
export class RangeDeviceSetupService implements IRangeDeviceSetupService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/DeviceIntegration/RangeServantIntegration';
  }
  Get(request: GetRangeDeviceSetupRequest): Observable<GetRangeDeviceSetupResponse>
  {
    const apiUrl = this.serviceUrl + '/GetGRangeSetup';
    return this.http.get<GetRangeDeviceSetupResponse>(apiUrl, { params: request as any }
    );
  }
  Post(request: PostGRangeDeviceSetupRequest): Observable<PostGRangeDeviceSetupResponse>
  {
    const apiUrl = this.serviceUrl + '/PostGRangeSetup';
    return this.http.post<PostGRangeDeviceSetupResponse>(apiUrl, request);
  }
}
